@font-face {
    font-family: "MuseoSans_500";
    src: url("./fonts/MuseoSans_500.otf") format("opentype");
}

@font-face {
    font-family: "MuseoSans_100";
    src: url("./fonts/MuseoSans_100.otf") format("opentype");
}

@font-face {
    font-family: "MuseoSans_300";
    src: url("./fonts/MuseoSans_300.otf") format("opentype");
}

@font-face {
    font-family: "MuseoSans_900";
    src: url("./fonts/MuseoSans_900.otf") format("opentype");
}

.App {
    text-align: center;
}

.clearfix {
    clear: both;
    overflow: auto;
}

* {
    -webkit-box-sizing: border-box !important;
            box-sizing: border-box !important;
    outline: none;
}

html,
body,
#root {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    background: white;
}

.App {
    margin: 15px;
    height: calc(100% - 30px);
    width: auto;
}

.App-header {
    background-color: #1C1F25;
    background-image: url("./images/background.jpg");
    background-size: cover;
    background-position: center;
    min-height: 100%;
    position: relative;
    overflow: auto;
    width: 100%;
    z-index: 1;
}

.App-header .menu-button.sexy .icon-bar:nth-child(1) {
    -webkit-transform: translateY(10px) rotate(45deg);
        -ms-transform: translateY(10px) rotate(45deg);
            transform: translateY(10px) rotate(45deg);
}

.App-header .menu-button.sexy .icon-bar:nth-child(2) {
    -webkit-transform: translateY(20px);
        -ms-transform: translateY(20px);
            transform: translateY(20px);
}

.App-header .menu-button.sexy .icon-bar:nth-child(3) {
    -webkit-transform: translateY(-6px) rotate(-45deg);
        -ms-transform: translateY(-6px) rotate(-45deg);
            transform: translateY(-6px) rotate(-45deg);
}

.App-header::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    background: #1C1F25;
    opacity: .95;
    z-index: 0;
}

.break {
    height: 20px;
    width: 120px;
    display: block;
    position: relative;
    margin-bottom: 5px;
    top: -30px;
}

.App-header nav {
    margin: 30px 0px 10px;
    position: relative;
    z-index: 2;
}

.App-header .menu-side .menu-list {
    padding: 0;
}

.App-header .menu-side .menu-list li {
    list-style: none;
    display: inline-block;
    padding: 10px;
}

.App-header .menu-side .menu-list li a {
    text-decoration: none;
    font-family: "Museosans_300";
    color: #d2d0d0;
}

.App-header .menu-side {
    -webkit-transition: 400ms;
    -o-transition: 400ms;
    transition: 400ms;
}

@media (max-width: 767px) {
    .App-header .menu-side.sexy {
        background: white;
        position: absolute;
        top: 65px;
    }
    .App-header .menu-side {
        background: white;
        position: absolute;
        top: -300px;
    }
    .App-header .menu-side li a {
        color: #4A4A4A !important;
        cursor: pointer;
    }
    .App-header .menu-side .menu-list,
    .App-header .menu-side .menu-list li {
        display: block;
    }
    .App-header .menu-button {
        background: none;
        border: none;
        float: right;
        display: block;
    }
    .App-header .menu-button>.icon-bar {
        height: 3px;
        margin: 5px 0px;
        width: 25px;
        display: block;
        background: white;
        border: none;
        -webkit-transition: 300ms;
        -o-transition: 300ms;
        transition: 300ms;
    }
}

@media(min-width: 768px) {
    .App-header .menu-button {
        display: none;
    }
    .App-header .menu-list {
        float: right;
    }
}

.App-header nav h4,
.App-header nav h5 {
    letter-spacing: .7px;
    color: white;
    margin: 0;
    text-align: left;
    margin: 2px 0px;
}

.App-header nav h4 {
    font-family: "MuseoSans_500";
}

.App-header nav h5 {
    font-family: "MuseoSans_100";
    font-size: 18px;
}

.brief-1,
.brief-2 {
    color: white;
    text-align: left;
    position: relative;
}

.brief-1 {
    padding-top: 65px;
}

.brief-1 h5,
.brief-1 p {
    font-size: 18px;
    line-height: 32px;
    letter-spacing: .7px;
}

.brief-1 h5 {
    font-family: "MuseoSans_300";
}

.brief-1 p {
    font-family: "MuseoSans_100";
    z-index: 10;
}

.brief-1 .say-hello {
    padding: 20px;
    display: inline-block;
    text-decoration: none;
    text-transform: none;
    color: white;
    font-family: 'Museosans_500';
    text-indent: 0%;
    background: #50E3C2;
}

.brief-2 {
    padding-top: 135px;
    padding-bottom: 50px;
}

.brief-2 h3 {
    font-family: "MuseoSans_500";
    font-size: 25px;
    letter-spacing: 1px;
    margin: 10px 0px;
}

.brief-2 h4 {
    font-family: "MuseoSans_500";
    font-size: 17px;
    letter-spacing: 1px;
    margin: 4px 0px;
}

.brief-2 p {
    font-family: "MuseoSans_100";
    margin: 0;
    margin-bottom: 18px;
    line-height: 30px;
}

.about-us {
    margin: 15px 0 0;
    background: #50E3C2;
    padding: 100px 0px;
}

.border-slide {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 0 0;
    border-right-width: calc(100vw - 30px);
    border-bottom-width: 200px;
    border-color: transparent #50E3C2 transparent transparent;
}

.about-us h3,
.about-us p {
    text-align: left;
    color: white;
}

.about-us h3 {
    font-family: "Museosans_900";
    letter-spacing: .7px;
    font-size: 28px;
    position: relative;
    margin-bottom: 10px;
    z-index: 1;
}

.break.green {
    background: #B8E986;
}

.about-us p {
    font-family: "Museosans_300";
    font-size: 20px;
    letter-spacing: .7px;
    line-height: 30px;
}

.about-us .me {
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 50%;
}

.break.blue {
    background: #93C5FF;
}

.selected-works h3.heading {
    font-family: "MuseoSans_900";
    font-size: 28px;
    letter-spacing: .7px;
    color: #4A4A4A;
    margin: 10px 0px;
    text-align: left;
    position: relative;
    z-index: 1;
}

.selected-works .works {
    padding-top: 100px;
    text-align: left;
}

.selected-works .works .template {
    text-align: left;
    padding: 60px 0px;
}

.selected-works .works .template h2 {
    font-family: "MuseoSans_900";
    font-size: 30px;
    letter-spacing: .6px;
    color: #4A4A4A;
    margin: 7px 0px;
}

.selected-works .works .template h4 {
    font-family: "MuseoSans_500";
    letter-spacing: .6px;
    color: #4A4A4A;
    font-size: 25px;
    margin: 15px 0px 5px;
}

.selected-works .works .template p {
    font-family: "MuseoSans_100";
    letter-spacing: .6px;
    font-size: 21px;
    color: #4A4A4A;
    margin: 15px 0px 5px;
}

.selected-works .works .template .collection {
    padding: 20px 0;
}

.selected-works .works .template .collection button.greyed {
    background: #F0F0F0;
    border: none;
    color: #4D4D4D;
    display: block;
    padding: 8px 5px;
    text-align: center;
    font-family: "Museosans_500";
    font-size: 14.5px;
    margin: 5px 0px;
    width: 90%;
}

.selected-works .works .showcase {
    -o-object-fit: contain;
       object-fit: contain;
    height: auto;
    width: 100%;
    position: relative;
    margin: 0 auto;
    -o-object-position: center;
       object-position: center;
}

.selected-works .works .template.two {
    position: relative;
}

.selected-works .works .showcase-small {
    -o-object-fit: contain;
       object-fit: contain;
    height: 150px;
    position: absolute;
    margin: 0 auto;
    -o-object-position: center;
       object-position: center;
}

.contact-us {
    background: #262626;
    padding: 100px 0px;
    text-align: left;
}

.contact-us h3 {
    color: white;
    font-family: "Museosans_500";
    letter-spacing: .7px;
}

.contact-us ul {
    padding: 0;
}

.contact-us ul li,
.contact-us ul li a {
    list-style-type: none;
    color: #9D9D9D;
    text-decoration: none;
    margin: 10px 0px;
    font-family: "Museosans_500";
}

.lighter-grey {
    background: #9D9D9D;
    padding: 10px 0px;
    font-family: "Museosans_500";
    letter-spacing: .5px;
    margin-bottom: 15px;
}


